.Inicio__container {
    width: 1000px;
    height: 800px;
    background-color: #00065c;
    margin: auto;
    position: relative;
}

.Changingarea {
    width: 43%;
    height: 90%;
    top: 50%;
    right: 4%;
    transform: translateY(-50%);
    background-color: #7f3063;
    box-shadow:0px 0px 25px #75c6b8;
    border-radius: 20px;
    position: absolute;
}

.Changingarea__img {
    width: 90%;
    border-radius: 20px;
}
